html, body {
    max-width: 100%;
    overflow-x: hidden;
    scroll-behavior: smooth; 
}
.App {
  padding: 2% 10%;
 display: grid;
 grid-template-areas: 
 "nav nav"
 "h h" 
 "a a"
 "s s" 
 "n n"
 "r r" 
 "q q"
 "ser ser"
 "o o"
 "f f";

 grid-template-rows: 60px 650px 400px 500px 300px 1fr 450px 300px 1fr 100px;
 grid-template-columns: 1fr 1fr;

}
.header{
  grid-area: h;
}

.about{
  grid-area: a;
}
.skills{
  grid-area: s;
}
.advantages{
  grid-area: n;
}
.reviews{
  grid-area: r;
}
.quotes{
  grid-area: q;
}

.services{
  grid-area: ser;
}

.order{
  grid-area: o;
}
.footer{
  grid-area: f;
}
.navbar{
  grid-area: nav;
}

@media(max-width: 1200px){
  .App {
   grid-template-rows: 60px 500px 400px 500px 300px 1fr 450px 300px 500px 100px;
 }
}

@media(max-width: 992px){
  .App {
   grid-template-rows: 60px 400px 400px 500px 300px 1fr 450px 300px 500px 100px;
 }
}

@media(max-width: 786px){
  .App {
   grid-template-rows: 60px 300px 400px 500px 300px 1fr 450px 300px 500px 100px;
 }
}
@media(max-width: 568px){
  .App {
   grid-template-rows: 60px 200px 400px 500px 300px 1fr 450px 300px 500px 100px;
 }
}
@media(max-width: 320px){
  .App {
   grid-template-rows: 60px 150px 400px 500px 300px 1fr 450px 300px 500px 100px;
 }
}