.quote{
	
	text-align: center;
}

.quote p{
	margin:10% 20% 2% 20%;
	color: #10c9c3;
	font-size: 30px;
}

.quote span{
	margin-bottom: 10%;
	color: #10c9c3;
	text-transform: uppercase;
}
@media(max-width: 992px){
  .quote p{
	margin:20% 0;
	color: #10c9c3;
	font-size: 20px;
}
}