
.header{

  position: relative;
  text-align: center;
  
  border-radius: 10px;
  display: block;
 
}
.header video{
  width: 100%;

  border-radius: 50%;
}

.header h1 {
  position: absolute;
	margin-top: 200px ;
  width: 100%;
  color: #fff;
 }
 
 @media(max-width: 992px){
  .header{
  height: 300px;
  
}
  .header h1 {
  
  margin-top: 150px ;
  
 }
 }

 @media(max-width: 768px){
  .header h1 {

  margin-top: 100px ;
  
  
 }
}
 @media(max-width: 576px){
  .header h1 {
  
  margin-top: 80px;
  
 }
}

 @media(max-width: 480px){
  .header h1 {
  
  margin-top: 50px;
  font-size: 20px;  
  
 }
}