.reviews{
	margin-top: 2%;


	text-align: center;
	background: #f4f8fb;
	padding: 0 50px;
	 border-radius: 10px;
}
.reviews h2{
	margin: 7% 0;
}
.review{
	
	background: #fff;
	
}
.review_slide{
	display: inline-block;
	vertical-align:top;
	padding: 20px;
	width: 35%;
	height: 80%;
	
}
.review_text{
	padding: 30px;
display: inline-block;	
vertical-align:top;
	
}

@media(max-width: 1301px){
	.review_slide{
		display: inline-block;
		vertical-align:top;
		padding: 7%;
		width: 20%;
		
	}
	.review_text{
	padding: 0;
	
	
}
}
@media(max-width: 1140px){
	.review{

	margin-bottom: 5%;
	
}
	.review_slide{
		display: block;
		
		margin:0 auto;
		width: 80%;
		
	}
	.review_text{
	padding: 0;
	display: block;	
	vertical-align:top;
	
}
}
@media(max-width: 992px){
	.review_slide{
		margin:0 auto;
		padding: 20px;
		width: 80%;
		
	}
}
@media(max-width: 568px){
	.reviews{

	padding: 0 10px;
	
}
	
}