.navbar{

	 display: inline-flex;
	 align-items: center;
	justify-content: space-between;
	border-radius: 4px;
	background-color: #141414;
	top:0;
	position:fixed;
	z-index: 100;
	width: 80%;
	
}

 .navbar_links {
 }
 .navbar_logo{
 padding: 7px;
  
  
 }
 .navbar_links a {   
	text-decoration: none;
	color:#fff;
	padding: 15px 10px;
 	font-size: 18px;
 	font-weight: 700;
 }

@media(max-width: 576px){
   .navbar_links a {   
	text-decoration: none;
	color:#fff;
	padding: 10px 5px;
 	font-size: 14px;
 	font-weight: 700;
 }
}

 @media(max-width: 500px){
 .navbar_links a {   
	text-decoration: none;
	color:#fff;
	padding: 10px 5px;
 	font-size: 12px;
 	font-weight: 700;
 }
}