.order{
	border-radius: 10px;
	background: #f4f8fb;
	text-align: center;
}
.order_header{
	margin:0 30%;
}
.order_form{
	display: block;
	margin:0 auto;
	width: 30%;
}

.order_form input{
	border: none;
	width: 100%;
padding: 10px;
	margin-bottom: 20px;
}
.order_form textarea{
border: none;
padding: 10px;
	width: 100%;
	margin-bottom: 20px;
	
}
.btn{
	background-color: #10c9c3;
	padding: 20px 35px;
	border: none;
	color:#fff;
	font-size: 12px;
	font-weight: 900;
	
}
@media(max-width: 992px){
	.order_header{
	margin:0 10%;
}
	 .order_form{
	display: block;
	margin:0 auto;
	width: 70%;
}
}
 @media(max-width: 576px){
  .order_form{
	display: block;
	margin:0 auto;
	width: 80%;
}
	.btn{
	background-color: #10c9c3;
	padding: 10px 15px;
	border: none;
	color:#fff;
	font-size: 12px;
	font-weight: 900;
	margin: 10% 0;
}

}